const state = {
	geolocation: {},
	currentPagination: 1,
};

const mutations = {
	setGetlocation(state, payload) {
		state.geolocation = payload;
	},
	setCurrentPagination(state, payload) {
		state.currentPagination = payload;
	},
};

const actions = { };

const getters = {
	getGeolocation: (state) => { return state.geolocation; },
	getCurrentPagination: (state) => { return state.currentPagination; },
};

export default {
	state,
	mutations,
	actions,
	getters,
};
