<template>
	<q-page padding>
		<div class="param-form" >
			<q-form @submit="setGetlocation">
				<div class="q-gutter-md">
					<div class="text-h4">找附近餐廳</div>
					<q-badge color="secondary">
						半徑: {{ radius }} 公尺
					</q-badge>
					<div style="width: 90%; margin: 25px;">
						<q-slider
						v-model="radius"
						:min="min"
						:max="max"
						:step="100"
						snap
						label
						label-always
						color="purple"
					/>
					</div>
					<q-btn label="找餐廳" type="submit" color="primary" />
				</div>
			</q-form>
		</div>
		<!-- <list-restaurants :radius="radius" /> -->
		<infinite-scroller :radius="radius"/>
	</q-page>
</template>

<script>
import { mapGetters } from 'vuex';
// import { filter, takeUntil } from 'rxjs/operators';
// import { from, timer } from 'rxjs';
// import ListRestaurants from '@/components/ListRestaurants.vue';
import InfiniteScroller from '@/components/InfiniteScroller.vue';

export default {
	name: 'home',
	components: {
		// HelloWorld,
		// ListRestaurants,
		InfiniteScroller,
	},
	data() {
		return {
			radius: 500,
			max: 1000,
			min: 100,
		};
	},
	computed: {
		...mapGetters(['getGeolocation']),
	},
	methods: {
		fetchRestaurantsList() {
			const payload = {
				location: this.getGeolocation,
				radius: this.radius,
				type: 'restaurant',
			};
			this.$store.commit('cleanList');
			this.$store.dispatch('fetchRestaurantList', payload);
		},
		setGetlocation() {
			this.$watchLocation().then((coordinates) => {
				console.log('coordinates', coordinates);
				const getlocation = {
					latitude: coordinates.lat,
					longitude: coordinates.lng,
				};
				this.$store.commit('setGetlocation', getlocation);
				localStorage.setItem('geolocationPermission', true);
				this.fetchRestaurantsList();
			});
		},
	},
};
</script>
