<template>
	<div class="q-pt-lg">
		<div class="text-h5 q-pb-sm">餐廳清單</div>
		<q-infinite-scroll @load="onLoad" :offset="250">
			<div v-for="(restaurant, index) in getRestaurants" :key="index">
				<q-card class="q-my-sm" @click="selected(restaurant)">
					<q-card-section>
						<div class="row no-wrap items-center">
							<div class="col text-h6 ellipsis">
								<q-icon :name="'img:' + restaurant.icon" />
								{{ restaurant.name }}
							</div>
						</div>
						<div class="row no-wrap items-center">
							<div class="col ellipsis">
								{{restaurant.vicinity}}
							</div>
						</div>
						<div class="row no-wrap items-center">
							<div class="col">
								<q-rating
								v-if="restaurant.rating"
								v-model="restaurant.rating"
								size="1em"
								color="positive"
								icon="star_border"
								icon-selected="star"
								readonly
								/>
								<q-rating
									v-else
									v-model="defaultRating"
									size="1em"
									color="positive"
									icon="star_border"
									icon-selected="star"
									readonly
								/>
							</div>
							<div class="col">
								{{restaurant.rating}} / {{ restaurant.user_ratings_total }} 人評分
							</div>
						</div>
						<div class="row no-wrap items-center">
						</div>
					</q-card-section>
				</q-card>
			</div>
			<template v-slot:loading>
				<div class="row justify-center q-my-md">
				<q-spinner-dots color="primary" size="40px"></q-spinner-dots>
				</div>
			</template>
		</q-infinite-scroll>
	</div>
</template>
<script>
import { mapGetters } from 'vuex';
import { filter, takeUntil } from 'rxjs/operators';
import { timer } from 'rxjs';

export default {
	name: 'InfiniteScroller',
	props: {
		radius: {
			type: Number,
			required: true,
		},
	},
	computed: {
		...mapGetters(['getNextPageToken', 'getGeolocation', 'getRestaurants']),
	},
	methods: {
		onLoad(index, done) {
			console.log('current', index);
			const timer$ = timer(100, 1000);
			const stop$ = timer$.pipe(filter(
				() => { return Object.keys(this.getGeolocation).length > 0; },
			));
			const result$ = timer$.pipe(takeUntil(stop$));
			this.$subscribeTo(
				result$,
				null,
				null,
				() => {
					const onSuccess = (hasNext = true) => {
						console.log('has next', hasNext);
						this.hasMoerData = hasNext;
						done(!hasNext);
					};
					if (this.getNextPageToken) {
						console.log('fetch next');
						this.$store.dispatch('fetchNextPage', onSuccess);
					} else {
						console.log('first fetch');
						const payload = {
							location: this.getGeolocation,
							radius: this.radius,
							type: 'restaurant',
							onSuccess,
						};
						this.$store.dispatch('fetchRestaurantList', payload);
					}
				},
			);
		},
		selected(restaurant) {
			this.$store.commit('setCurrentPagination', this.current);
			this.$router.push({ path: `/restaurant/${restaurant.place_id}` });
		},
	},
	data() {
		return {
			defaultRating: 0,
		};
	},
};
</script>
